<template lang="pug">
    div.content-account
        div.list-block.account-bar
            ul
                li.item-content
                    div.item-inner
                        div.item-title
                            label 账户余额：
                            span
                                img.icon-diamond(src='../../../assets/img/icon-diamond.png' height="16")
                                span(v-text="diamonds")
                        div.item-after
        div.list-block.media-list.recharge-list
            ul
                li.item-content(v-for="item in rule_list")
                    div.item-inner
                        div.item-title-row
                            div.item-title
                                img.icon-diamond(src='../../../assets/img/icon-diamond.png' height="16")
                                span(v-text="item.diamonds")
                        div.item-subtitle
                            span(v-text="item.name")
                    div.item-after
                        div.btn-recharge(
                            v-text="item.money"
                            @click="pay(item.id)"
                        )
</template>
<script>
import queryString from 'queryString'
export default {
    name: 'accountIndex',
    data() {
        return {
            diamonds: '',
            rule_list: ''
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/payRecharge' : this.api.payRecharge();
            this.axios.get(axiosUrl)
                .then(res => {
                    this.diamonds = res.data.diamonds;
                    this.rule_list = res.data.rule_list;
                })
                .catch(err => {
                    console.log(err);
                });
        },

        /**
         * @method:请求支付
         */
        pay(rule_id) {
            this.axios.post(this.api.payWeixin(), queryString.stringify({
                rule_id: rule_id
            }))
            .then(res => {
                if(res.data.status == 1) {
                    this.weixinPay(result.data); // 微信支付
                }
                else {
                    this.Toast(res.data.error);
                }
            })
            .catch(err => {
                this.Toast(err);
            });
        },

        /**
         * @method :微信支付方法
         * @param data
         */
        weixinPay: function(data) {
            var self = this;
            if (typeof WeixinJSBridge == "undefined") {//微信浏览器内置对象。参考微信官方文档
                if (document.addEventListener) {
                    document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady(data), false);
                } else if (document.attachEvent) {
                    document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady(data));
                    document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady(data));
                }
            } else {
                self.onBridgeReady(data);
            }
        },

        /**
         * @method 支付费用方法
         * @param data:后台返回的支付对象,(详情微信公众号支付API中H5提交支付);
         */
        onBridgeReady: function(data) {
            var self = this;
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest', {
                    "appId": data.appId, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                    "timeStamp": data.timeStamp, //时间戳
                    "nonceStr": data.nonceStr, // 支付签名随机串，不长于 32 位
                    "package": data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                    "signType": data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                    "paySign": data.paySign // 支付签名
                },
                function(res) {
                    // 支付成功
                    if (res.err_msg == "get_brand_wcpay_request：ok") {
                        Toast('支付成功');
                    }
                    // 支付失败
                    else {
                        alert("支付失败,请跳转页面" + res.err_msg);
                    }
                }
            );
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.icon-diamond {
    margin-right: .3rem;
}

.account-bar {
    label {
        margin-right: .3rem;
    }
}

.recharge-list {
    .item-subtitle {
        font-size: .6rem;
        color: @fc-light;
    }
    .btn-recharge {
        width: 3.4rem;
        height: 1.5rem;
        line-height: 1.5rem;
        text-align: center;
        border: 1px solid @color-theme;
        border-radius: 1.5rem;
    }
    .item-content {
        &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            right: auto;
            top: auto;
            height: 1px;
            width: 100%;
            background-color: #e7e7e7;
            display: block;
            z-index: 15;
            -webkit-transform-origin: 50% 100%;
            transform-origin: 50% 100%;
            -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
        }
    }
    .item-inner {
        &:after {
            content: none;
        }
    }
    .item-after {
        margin-right: .75rem;
    }
}
</style>



